
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import { mapGetters, mapState } from 'vuex'
import { fetchAnnouce, fetchRyToken } from './api'
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/user-circle'
import { MEMBER } from './customConfig'
import { init } from './utils/initRy.js'
import api from './api/urls'
export default {
name: 'app',
components: {
ViewBox,
Tab,
TabItem,
Icon,
AlertModule,
XHeader
},
data () {
return {
index: 0,
RECEIVER: '',
websocket: '',
firstInit: true
}
},
computed: {
...mapGetters([
'user'
]),
...mapState([
'systemConfig', 'chatlist', 'ws', 'messages'
]),
avatar () {
return {
'background-image': `url(${this.user.avatar})`
}
},
pages () {
if (this.user.viewRole && this.user.viewRole === MEMBER) {
return [{
name: '聊天室',
path: '/chatroom'
},




{
name: '开奖',
path: '/results'
},




{
name: '投注',
newPage: this.$store.state.systemConfig.mobileLotteryUrl
},
{
name: '充值中心',
path: '/recharge'
},
{
name: '帐户',
path: '/my'
}]
}
return [{
name: '聊天室',
path: '/chatroom'
},
{
name: '开奖',
path: '/results'
},




{
name: '投注',
newPage: this.$store.state.systemConfig.mobileLotteryUrl
},
{
name: '充值中心',
path: '/recharge'
},
{
name: '帐户',
path: '/my'
}]
},
chatWithTitle () {
const chatWithId = parseInt(this.$route.params.chatWithId)
if (chatWithId) {
const index = this.$store.state.chatlist.findIndex(member => member.id === chatWithId)
if (index !== -1) {
return `客服人员${index + 1}`
}
}
return ''
},
isSubPage () {
return this.$route.meta.showBack
},
needBackBtn () {
if (this.isSubPage) {
if (this.$route.path === '/login' && !this.user.logined) {
return false
}
return true
}
return false
}
},
watch: {
'user.logined': {
handler: function (val, oldVal) {
if (val === true && oldVal === false) {
this.getRyToken()
}
},
deep: true
}
},
created () {
fetchAnnouce().then(result => {
const announcement = []
result.forEach((item) => {
if (item.platform !== 0) {
announcement.push(item.content)
}
})
this.$store.dispatch('setAnnouncement', announcement)
})
},
mounted () {
this.beforeJoinChatRoom()
},
methods: {
switchTab (page, index) {
if (page.path) {
this.$router.push({path: page.path})
this.$refs['tab']['_data'].currentIndex = index
} else {
this.$router.push({path: this.pages[0].path})
this.$refs['tab']['_data'].currentIndex = 0
}
},
leaveRoom (fn) {
if (!this.ws) return
this.$store.commit('CLEAR_MESSAGE')
this.ws.quitChatRoom(this.RECEIVER, {
onSuccess: function () {
fn && fn()
},
onError: function () {
console.log('退出聊天室失败')
}
})
},
beforeJoinChatRoom () {
this.$store.commit('UPDATE_CONNECT', {
chatLoading: true
})








},
getRyToken () {
fetchRyToken().then((res) => {
this.ryToken = res.chat_token
this.RECEIVER = String(this.user.default_room_id) || (res.room_info.length ? res.room_info[0].id : '1')
this.connectRongCloud(res.chat_token)
}).catch(error => {
console.log(error)
})
},
connectRongCloud (token) {
let params = {
appKey: api.appKey,
token: token,
store: this.$store
}
init(params, {
getInstance: (ws) => {
this.$store.dispatch('setWebsocket', ws)
this.joinChatRoom()
},
receiveNewMessage: this.handleMsg
})
},
joinChatRoom () {
this.$store.commit('CLEAR_MESSAGE')
this.ws && this.ws.joinChatRoom(this.RECEIVER, 30, {
onSuccess: () => {
this.$store.commit('UPDATE_CONNECT', {
chatLoading: false
})
this.$store.commit('UPDATE_CONNECT', {
status: 0
})
this.firstInit = false
this.$emit('chatRoomReady', true)
},
onError: (error) => {
console.log(error)
}
})
},
handleMsg (message) {
let data = message.content
let robotMsg = data.message

if (robotMsg) {
if (typeof robotMsg.content !== 'object') {
return
}
let robotData = {}
robotData.content = robotMsg.content.content
robotMsg.content.type = Number(robotMsg.objectName)
robotData.extra = JSON.stringify(Object.assign({}, robotMsg.content))

data = robotData
}
let extra = JSON.parse(data.extra)
data.extra = extra
switch (data.extra.type) {
case 5:
if (data.extra.sender && data.extra.sender.avatar !== null) {
if (data.extra.sender.avatar.indexOf('//upload') !== -1) {
data.extra.sender.avatar = data.extra.sender.avatar.replace('//upload', '/upload')
}
}
data.extra.envelope_status.users = []
break
case 6:
if (data.extra.sender && data.extra.sender.avatar !== null) {
if (data.extra.sender.avatar.indexOf('//upload') !== -1) {
data.extra.sender.avatar = data.extra.sender.avatar.replace('//upload', '/upload')
}
}

let currentEnvelopeIndex = this.messages.findIndex((msg) => msg.envelope_status && msg.envelope_status.id === data.envelope_status.id)
this.$store.commit('SWITCH_ENVELOPE_STATUS', {
index: currentEnvelopeIndex,
status: data.envelope_status
})
break
default:
if (data.command === 'live' || data.command === 'read_msg') {
return
}
if (extra.type === 0) {
data.content = unescape(data.content)
}
if (data.extra.sender && data.extra.sender.avatar !== null) {
if (data.extra.sender.avatar.indexOf('//upload') !== -1) {
data.extra.sender.avatar = data.extra.sender.avatar.replace('//upload', '/upload')
}
}

if (extra.type === 7 && data.content && data.content !== null) {
if (data.content.indexOf('//upload') !== -1) {
data.content = data.content.replace('//upload', '/upload')
}
}
break
}

if (data.extra.type === 6 && data.extra.sender.id !== this.user.id) {
return
}
this.$store.dispatch('addMessage', data)
}
}
}



