import Vue from 'vue'
import * as types from './mutation-types'

export default {
  [types.SET_USER]: (state, { user }) => {
    Vue.set(state, 'user', {
      ...state.user,
      ...user
    })
  },
  [types.RESET_USER]: (state) => {
    state.user = {
      logined: false
    }
    state.chatlist = []
    state.envelope = {}
    Vue.cookie.delete('access_token')
    Vue.cookie.delete('refresh_token')
  },
  [types.SET_SYSTEM_CONFIG]: (state, data) => {
    state.systemConfig = data
  },
  [types.SET_WS]: (state, ws) => {
    state.ws = ws
    clearInterval(state.hearbeat)
    state.hearbeat = setInterval(() => {
      ws.send(JSON.stringify({
        'command': 'live',
        'user_id': state.user.id
      }))
    }, 30000)
  },
  [types.ADD_MESSAGE]: (state, message) => {
    state.messages.push(message)
  },
  [types.SWITCH_ENVELOPE_STATUS]: (state, {index, status}) => {
    let data = state.messages[index]
    if (data) {
      data.extra.envelope_status = Object.assign({}, data.extra.envelope_status, status)
      Vue.set(state.messages, index, data)
    }
  },
  [types.CLEAR_MESSAGE]: (state, message) => {
    state.messages = []
  },
  [types.SET_ANNOUNCE]: (state, announcement) => {
    state.announcement = announcement
  },
  [types.INIT_PERSONAL_SETTING]: (state, setting) => {
    state.personal_setting.banned = setting.banned
    state.personal_setting.blocked = setting.blocked
  },
  [types.UPDATE_PERSONAL_SETTING]: (state, type) => {
    switch (type) {
      case 'unblock':
        state.personal_setting.blocked = false
        break
      case 'unbanned':
        state.personal_setting.banned = false
        break
      case 'blocked':
        state.personal_setting.blocked = true
        break
      case 'banned':
        state.personal_setting.banned = true
        break
    }
  },
  [types.INIT_CHATLIST]: (state, chatlist) => {
    state.chatlist = chatlist
  },
  [types.UPDATE_READ_STATUS]: (state, {id, status}) => {
    const target = state.chatlist.find((member) => member.id === id)
    if (target) {
      target.room.read = status
    }
  },
  [types.UPDATE_CONNECT]: (state, data) => {
    // state.connectStatus = {
    //   status: '',
    //   send: 0
    // }
    state.connectStatus = Object.assign({}, state.connectStatus, data)
  },
  [types.REFRESH_ROOM]: (state, loading) => {
    state.refreshRoom++
  },
  [types.SET_WEBSOCKET]: (state, data) => {
    state.ws = data
  },
  [types.INIT_EMOJI]: (state, {id, emojis}) => {
    Vue.set(state.emojis, id, emojis)
  }
}



