import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      logined: false,
      memberLogined: false
    },
    systemConfig: {
      mobileLotteryUrl: '',
      customerServiceUrl: '',
      webLotteryRegisterUrl: '',
      lotteryResultUrl: '',
      envelopeSettings: {},
      stickerGroups: [],
      checkinSettings: {}
    },
    emojis: {
      symbol: []
    },
    ws: null,
    connectStatus: {
      status: '',
      send: 0,
      chatLoading: true
    },
    hearbeat: null,
    rooms: {
      1: []
    },
    messages: [],
    personal_setting: {
      banned: false,
      blocked: false
    },
    announcement: [],
    chatlist: [],
    envelope: {},
    today: new Date()
  },
  actions,
  mutations,
  getters
})



